img{
    width:100%;
    height: auto;
    margin: 0;
}
.home-wrapper{
    width:100%;
    height: auto;
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    position: relative;
   
}


.first-view{
    width: 100%;
    height: 300px;
    background-color: #d8d8d8;
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    overflow: hidden;
}

.full-image{
    max-width:100%;
    width:100%;
    height: 100%;
    overflow: hidden;
    background:linear-gradient(to left,transparent 20%, #d8d8d8 120%);
    background-position: center;
    /* background-origin: left; */
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    display: -webkit-flex;
    flex-direction:flex-start;
    justify-content: flex-start;
}
.flex-classes{
    width:100%;
    height: 100%;
    background-color:#d8d8d8;
    display: flex;
    display: -webkit-flex;
    flex-direction:flex-start;
    justify-content: flex-start;
    color: white;
  
}
.flex-class{
    display: flex;
    display: -webkit-flex;
    flex:1;
    -webkit-box-flex: 1;
    position: relative;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-size:cover;
    background-position: center;
    background-repeat: no-repeat;
    filter:grayscale(100%);
    transition: font-size 0.7s cubic-bezier(0.45, 0.01, 0.33, 0.24),
            filter 0.7s cubic-bezier(0.45, 0.01, 0.33, 0.24),
            flex 0.7s cubic-bezier(0.45, 0.01, 0.33, 0.24),
            background 0.2s;
    
}


 .flex-class > *{
    filter:grayscale(0%);
    margin: 0;
    width: 100%;
    transition: transform 0.5s;
    flex: 0 0 auto;
   -webkit-flex: 0 0 auto;
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    height: 90px;
    align-items: center;
    text-transform: uppercase;
    text-shadow: 0 0 4px rgba(0, 0, 0, 0.72), 0 0 14px rgba(0, 0, 0, 0.45);
    font-size: 1em;
  } 
  .flex-class >p{
      height: 90px;
      width:95%;
  } 
  .flex-class > button {
    font-size: 14px;
    width:50%;
    height: 50px;
    background:transparent;
    background-color: transparent;
    border-color:rgba(1,1,1,0.2);
    border-style: solid;
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    align-items:flex;
    margin-top:3rem;
  }
  .flex-class.flex-class-open,.flex-class.flex-class-open:active{
    flex:5;
    -webkit-box-flex: 5;
    filter:grayscale(30%);
}

  .flex-class.flex-class-open > button{
     background: var(--base-color);
    line-height: 1;
    width:70%;
    font-size:16px;
    font-weight: bold;
  }


.flex-class:nth-child(1){ background-image:url(https://source.unsplash.com/d2MSDujJl2g/1500x1500); }
.flex-class:nth-child(2) { background-image:url(https://source.unsplash.com/iRsktlI-hls/1500x1500)}
.flex-class:nth-child(3){ background-image:url(https://source.unsplash.com/YGmk9UZMdZg/1500x1500); }



.flex-class > *:first-child { transform: translateY(-150%); font-size:20px;}
.flex-class.flex-class-open > *:first-child { transform: translateY(0); transition-delay: 0.4s; font-weight: bold; }
.flex-class > *:last-child { transform: translateY(150%);font-size:14px; }
.flex-class.flex-class-open > *:last-child { transform: translateY(0);transition-delay: 0.4s; }

.thirdDiv{
    position:relative;
    width: 100%;
    height: auto;
    display: flex;
    display: -webkit-flex;
    justify-content: flex-start;
    text-align: center;
    flex-direction: column;
    z-index: 3;
    background-color: #d8d8d8;
    margin-top: 5rem;
    padding:0 1rem;
}
.thirdDiv h2{
    background-color: #d8d8d8;
    width:300px;
    margin: 0 auto;
}
.thirdDiv::before{
    content:'';
    position: absolute;
    z-index: -1;
    left: 0;
    margin-left:1%;
	top:0.8rem;
    width: 97.5%;
    height:100%;
    border:1px solid var(--base-color);
    /* background-color: ; */
}

.discount{
    display: none;
    visibility: hidden;
}
.secondDiv,.fourthDiv{
    height: 0px;
    position:relative;
    width: 100%;
    background-color: #d8d8d8;
}
.secondDiv{
    height: 63px;
}
 .secondDiv:after,.fouthDiv:after{
    position:absolute;
    width: 100%;
    height: 60px;
    content: '';
    top:0;
    right:0;
    left:0;
    z-index:4;
    transform-origin: top left;
    transform: skewY(-3deg);
    overflow: hidden;
    
}


.slanted{
    font-size: 20px;
    position: absolute;
    width: 100%;
    height: 60px;
    top: 0;
    left: 0;
    bottom:0;
    text-align: center;
    text-transform: uppercase;
    transform-origin: top left;
    transform: rotate(-3deg) skew(-3deg);
    z-index:10;
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    margin: 0;
    cursor: pointer;
    transition: all 0.5s ease-in;
    font-weight: bold;
    background-color: var(--slanted-color);
}
.fourthDiv .slanted{
    background-color: var( --base-color);
}

.slanted span{
    padding-left: 0.5rem;
    font-size: 30px;
}

.slanted::before{
	content:'';
    position: absolute;
    z-index: -10;
	left: 0px;
	/* bottom: 0px; */
	width: 0;
	height: 100%;
	background: var(--linear-menu-color);
    transition: all 0.5s ease-in;

}
.slanted:hover:before, .slanted:hover, .slanted:active:before,.slanted:active  {
   width:100%;
   font-size: 1.40rem;
}



.treatments{
    width:100%;
    height: auto;
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    z-index: 10;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 1rem;
    background-color: #d8d8d8;
}

.treatments h2{
    width:100%;
    padding-top: 2.5em;
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    align-items: center;
    margin:0;
      /* background: linear-gradient(to right, rgb(255, 138, 0), rgb(218, 27, 96));
   -webkit-text-fill-color: transparent;
   -webkit-background-clip: text; */
}
