.treatments p{
    width:100%;
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    align-items: center;
    text-align: left;
}
.treatment-image{
    width:300px;
    height:300px;
    display: flex;
    display: -webkit-flex;
    justify-content:flex-end;
    align-items: flex-end;
    background-color: #d8d8d8;
    margin: 0 auto;
    z-index:99;
   }

.fifthDiv{
    position:relative;
    width: 100%;
    height: auto;
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: nowrap;
    text-align: center;
    flex-direction: row;
    background-color: #d8d8d8;
    margin-bottom: 5rem;
}

.fifthDiv h4{
    width:100%;
    height: 30%;
    text-align: center;

}

.partners{
    width:100%;
    height:auto;
    padding-top: 3rem;
    position: relative;
    z-index: 3;

}
.partners h2{
    margin-top: 1rem;
    background-color: #d8d8d8;
    width:210px;
    margin: 0 auto;
}
.partners ::before{
    content:'';
    position: absolute;
    z-index: -1;
    left: 0px;
    top:4rem;
    bottom:0;
    width: 97.5%;
    margin-left: 1%;
    height:100%;
   border:1px solid var(--base-color);
}

.partnersImages{
    width:100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    padding-top: 2rem;
}
.partnersImages .part {
    width:200px;
    height: 100px;
    margin: 0.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.treatmentButtons{
    width: 100%;
    height: 100px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    /* transform: rotate(-3deg); */
    z-index: 100;
}
.treatmentButton{
    width:220px;
    height: 50%;
    font-size: 1.1rem;
    font-weight: bold;
    /* border: 1px solid black; */
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    cursor: pointer;
    background: var(--base-color);
    border-radius: 0.7rem;
    transition: all 0.4s ease-in;
    display: flex;
    justify-content: center;
    align-items: center;
}
.treatmentButton >*{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.treatmentButton:hover{
    transform: scale(1.1);
    background: var(--linear-menu-color);
}

@media screen and (min-width: 475px) {
    .WelcomeText{
        font-size: 1.5rem;
    }
    .WelcomeText span{
        font-size: 2.2rem;
    }
}


@media screen and (min-width: 670px) {
    .first-view{
        height:60vh;
    }
    .treatments p{
        width:80%;
    }
    .flex-class > p{
        height: 200px;
    }
    .flex-class.flex-class-open > *:first-child {font-size:2.0rem;}
    .flex-class.flex-class-open > *:last-child {font-size:1.3rem;}
    .full-image{
        background-position: center;
    }
    .thirdDiv{
        height: 470px;
        flex-direction: column;
    }
    .thirdDiv-inner{
        display: flex;
        flex-direction: row;
    }
    .treatments{
        width:50%;
    }
  
    .thirdDiv-inner{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        width:100%;
        height: 100%;
    }
    .treatment-image{
        max-width: 350px;
        height:100%;
        display: flex;
        justify-content:center;
        align-items:center;
        overflow: hidden;
       }
       .fifthDiv{
          height:auto;
       }
}

@media screen and (min-width: 850px) {
    .first-view{
        height:80vh;
    }
    .full-image{
        background-position:center;
        display: flex;
        flex-direction: column;
        justify-content:flex-end;
        align-items: flex-end;
        flex-wrap: nowrap;
    }
    .discount{
        width: 100%;
        height: auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-right: 3rem;
        visibility: visible;
    }
    .discount span{
        font-size:1.1rem;
        font-weight: bold;
        text-transform: uppercase;
    }
    .discount p:first-child{
        font-size:1.1rem;   
        animation: pulse 2s infinite ease-in-out;  
        width: 60%;
        display: flex;
        justify-content: flex-end;
      
    }
    @keyframes pulse {
        0% {
            background: transparent;
        }
        100% {
            background: var(--linear-menu-color);
            -webkit-text-fill-color: transparent;
            -webkit-background-clip: text;
        }
      }
    .thirdDiv{
        height: 430px;
    }
    .secondDiv{
        height: 83px;
    }
    .treatments{
        width:65%;
    }
    
    .treatment-image{
        width:50%;
        max-width: 430px;
    }

    .slanted{
        width: 100%;
        height: 80px;
        font-size: 1.5rem;
    }
    .slanted:hover:before, .slanted:hover .slanted:active:before, .slanted:active {
        font-size: 1.70rem;
     }
     .flex-classes{
         width:100%;
         height:95%;
     }
    .flex-class.flex-class-open > *:first-child {font-size:2.3rem;}
    .flex-class.flex-class-open > *:last-child {font-size:1.6rem;}
    .flex-class.flex-class-open > button{
        width:50%;
    }
}