.about-wrapper{
    width:100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: nowrap;
}

.about-first-view{
    width:100%;
    background-color: var(--base-color);
    height: 15vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
}
.about-first-view::before{
    content:'';
    position: absolute;
    z-index: -1;
	left: 0px;
	/* bottom: 0px; */
	width: 100%;
	height:19vh;
    background-color: var(--base-color);
    transform: rotate(-3deg);
    transform-origin: bottom;
}

.about-detail{
    width:100%;
    height: auto;
    padding-top: 4vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* flex-wrap: wrap; */
    padding-left: 1rem;
    padding-right:1rem;
}

.about-picture{
    width:90%;
    height: 200px;
    /* background-color: green; */
    background: url(https://source.unsplash.com/d2MSDujJl2g/1500x1500);
    background-position: center;
    object-fit: cover;
    background-size: cover;
    background-repeat: no-repeat;
}

.about-detail-info{
    width:100%;
    height: auto;
   padding-right: 1rem;
}
.meet-doctor{
    width:100%;
    height: auto;
    margin-top: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 1rem 2rem;
    position: relative;
    margin-bottom: 1.5rem;
}
.meet-doctor > h3{
    font-size: 1.8rem;   
    background-color: #d8d8d8;
    padding-left: 10px;
    padding-right: 10px;
    text-align: center;
}

.meet-doctor >h3::before{
 
    top:30px;
    content:'';
    position: absolute;
    z-index: -1;
    left: 0px;
    top:32px;
    bottom:0;
    width: 97.5%;
    margin-left: 1%;
    height:93%;
   border:1px solid var(--base-color);
}

.doctor-detail {
    width:100%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.doctor-picture{
    width: 200px;
    height: 200px;
    border-radius:50%;
    border:1px solid black;
    background: url(https://source.unsplash.com/d2MSDujJl2g/1500x1500);
    background-position: center;
    object-fit: cover;
    background-size: cover;
    background-repeat: no-repeat;
}

@media screen and (min-width: 670px) {
    .about-first-view::before{
        height:20vh;
    }

}
@media screen and (min-width: 800px) {
    .about-first-view::before{
        height:22.7vh;
    }
    .about-first-view{
        font-size: 2.5rem;
    }
    .about-detail{
        flex-direction: row;
        margin-top:2rem;
        justify-content: space-around;
        height:50vh;
    }
    .about-picture{
            width:300px;
            height: 300px;
    }

    .about-detail-info{
        width:50%;
    }
    .doctor-detail{
        flex-direction: row-reverse;
        justify-content: space-around;
        height:40vh;
    }
    .doctor-detail p{
        width:50%;
    }

}
@media screen and (min-width: 1000px) {
    .about-first-view::before{
        height:25.3vh;
    }
    .about-detail{
        margin-top:3rem;
    }
    .about-picture{
        width:500px;
    }

}