.footer{
    background-color: var(--footer-color);
    width:100%;
    height: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items:center;
    flex-wrap: nowrap;
    position: relative;
    z-index: 1000;
}

.AddressInfo{
    width: 55%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items:center;
}
.Footerinfo{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items:flex-start;
    width: 100%;
    font-size: 1.10rem;
    margin-top: 1rem;
    padding-left: 1rem;
}
.AddressFonts{
    font-size: 1.2rem;
    padding-right: 0.3rem;
}
.quickLinks{
    width:43%;
    /* background-color: aliceblue; */
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding-top: 0.5rem;
    padding-left: 1rem;
}
.quickMenuContent{
    padding-top: 0.3rem;
    padding-bottom: 0.3rem;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding-left: 1rem;
    color:black;
}
.quickMenuContent ul,.quickMenuContent li ,.quickMenuContent li a{
    width:100%;
    height: auto;
}

.footerbrief{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items:center;
    /* background-color: green; */
}
.socialFonts{
    width: 90%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items:center;
    padding-top: 1rem;
    padding-bottom: 1rem;
    font-size: 2rem;
}

.footerAboutUs{
    display: none;
    visibility: hidden;
}

@media screen and (min-width: 670px){
    .quickLinks{
       flex-direction: row;
       width:450px;
       justify-content: space-around;
       align-items:flex-start;
    }
    .AddressInfo{
        width:280px;
    
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
    }
    .Footerinfo{
        justify-content: flex-start;
        align-items: flex-start;
    }
    .footerAboutUs{
        display: flex;
        padding-top: 0.3rem;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        visibility:visible;
        font-size: 0.75rem;
    }
    .footer{
        display: flex;
        justify-content: center;
        align-items:flex-start;
        margin: 0 auto;
    }
    .footerAboutUs h3{
        font-size: 1.2rem;
        margin: 0;
        padding: 0;
    }
    .footerAboutUs p{
        margin: 0;
        padding-top:0.3rem;
    }
  
}