@import url(https://fonts.googleapis.com/css?family=Proza+Libre);
*{
  box-sizing: border-box;
  font-family: 'Proza Libre', sans-serif;

}
:root{
  /* --base-color:#FFDAD6; */
  --menu-color:#C06C84;
  /* --menu-color :#D2E9E1; */
  --base-color :#5680E9;
  --linear-menu-color : linear-gradient(0.25turn,#5680E9, #C06C84);
  --slanted-color: #C06C84;
  --footer-color: #565656;
}

li,a,ul,h2,h1,h3{
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  color:black;
}
h1,h2,h3,h4{
  text-shadow: 0 0 2px rgba(0, 0, 0, 0.72), 0 0 14px rgba(0, 0, 0, 0.45);
}
body {
  margin: 0 auto;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  max-width:1450px;
  background-color: #d8d8d8;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{
  position: relative;
  margin: 0;
  padding: 0;
}


.navbar {
    width:100%;
    height:10vh;
    position: relative;
    top:0;
    right:0;
    left:0;
    z-index:10001;
  }
  .header{
    width:100%;
    height: 10vh;
    background:-webkit-gradient(linear, left top, left bottom, color-stop(90%, transparent), color-stop(130%, #d8d8d8)),var(--linear-menu-color);
    background:-webkit-linear-gradient(top, transparent 90%, #d8d8d8 130%),var(--linear-menu-color);
    background:linear-gradient(to bottom, transparent 90%, #d8d8d8 130%),var(--linear-menu-color);
    object-fit: cover;
    position: fixed;
    overflow: hidden;
    top:0;
    right: 0;
    left:0;
    display: flex;
    flex-basis: row;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: center;
    padding-left: 1rem;
    padding-right: 1rem;
    font-family: 'Proza Libre', sans-serif;
    
  }
  
  .logo{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }
  
  .menu {
    display: none;
  } 
  .menu-sideDrawer{
    width: 320px;
    background:-webkit-linear-gradient(288deg,var(--base-color),var(--slanted-color));
    background:linear-gradient(0.45turn,var(--base-color),var(--slanted-color));
    position: fixed;
    top:0;
    left:0;
    height: 100%;
    z-index:10000;
    -webkit-transform:translateX(-100%);
            transform:translateX(-100%);
    -webkit-transition: -webkit-transform 0.5s ease-in;
    transition: -webkit-transform 0.5s ease-in;
    transition: transform 0.5s ease-in;
    transition: transform 0.5s ease-in, -webkit-transform 0.5s ease-in;
  }
  .menu-sideDrawer .menu {
    width:100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-end;
    align-items:center;
    -webkit-transform:translateX(-100%);
            transform:translateX(-100%);
    -webkit-transition: -webkit-transform 0.8s cubic-bezier(0.45, -0.25, 0.29, 0.38);
    transition: -webkit-transform 0.8s cubic-bezier(0.45, -0.25, 0.29, 0.38);
    transition: transform 0.8s cubic-bezier(0.45, -0.25, 0.29, 0.38);
    transition: transform 0.8s cubic-bezier(0.45, -0.25, 0.29, 0.38), -webkit-transform 0.8s cubic-bezier(0.45, -0.25, 0.29, 0.38);
  }
  
  .menu-sideDrawer.open .menu{
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  
  .menu-sideDrawer .menu .menu-wrapper{
    width:100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
    align-items:center;
    padding-left:1rem;
    padding-right:1rem;
  
  }
  .menu-sideDrawer.open{
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  .hamburger-menu{
    width: 20px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.7rem;
    position: absolute;
    cursor: pointer;
  }
   
  .menu-sideDrawer .links{
    width:100%;
    height:50px;
    display: flex;
    justify-content: center;
    align-items: center;
    border:1px solid black;
    font-size: 2rem;
    margin-top: 1rem;
    margin-bottom:1rem;
  }
 .links a{
    width: 100%;
    height: 100%;
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    padding:0 2px;
  }
  .hamburger-menu .fa-times{
    display:none;
}
  .hamburger-menu.open .fa-times{
      display:block;
 
  }
.hamburger-menu.open .fa-bars{
    display:none;
    overflow: hidden;

} 
/*   
.hamburger-menu.open {}
  
   */
  
  
  
  @media screen and (min-width: 800px) {
    .menu-sideDrawer,.hamburger-menu{
      display: none;
      overflow: hidden;
    }
   
    .logo{
        width:200px;
    }
    .menu {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        align-items: center;
    }
    .menu .menu-wrapper{
        width:85%;
        max-width: 700px;
        height: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: flex-end;
    }
    .menu .links{
        /* width:20%; */
        /* padding-left: 4px;
        padding-right: 4px; */
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        color:white;
        border-bottom:3px solid transparent;
        
    }
     .links:hover {
       
        border-bottom:3px solid var(--base-color);
    } 
    .links:hover a{
      -webkit-transform: scale(1.15) skew(-3deg);
              transform: scale(1.15) skew(-3deg);
        background-color:transparent;
    }
  }
img{
    width:100%;
    height: auto;
    margin: 0;
}
.home-wrapper{
    width:100%;
    height: auto;
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    position: relative;
   
}


.first-view{
    width: 100%;
    height: 300px;
    background-color: #d8d8d8;
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    overflow: hidden;
}

.full-image{
    max-width:100%;
    width:100%;
    height: 100%;
    overflow: hidden;
    background:-webkit-gradient(linear,right top, left top,color-stop(20%, transparent), color-stop(120%, #d8d8d8));
    background:-webkit-linear-gradient(right,transparent 20%, #d8d8d8 120%);
    background:linear-gradient(to left,transparent 20%, #d8d8d8 120%);
    background-position: center;
    /* background-origin: left; */
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    display: -webkit-flex;
    flex-direction:flex-start;
    justify-content: flex-start;
}
.flex-classes{
    width:100%;
    height: 100%;
    background-color:#d8d8d8;
    display: flex;
    display: -webkit-flex;
    flex-direction:flex-start;
    justify-content: flex-start;
    color: white;
  
}
.flex-class{
    display: flex;
    display: -webkit-flex;
    flex:1 1;
    -webkit-box-flex: 1;
    position: relative;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-size:cover;
    background-position: center;
    background-repeat: no-repeat;
    -webkit-filter:grayscale(100%);
            filter:grayscale(100%);
    -webkit-transition: font-size 0.7s cubic-bezier(0.45, 0.01, 0.33, 0.24),
            flex 0.7s cubic-bezier(0.45, 0.01, 0.33, 0.24),
            background 0.2s,
            -webkit-filter 0.7s cubic-bezier(0.45, 0.01, 0.33, 0.24);
    transition: font-size 0.7s cubic-bezier(0.45, 0.01, 0.33, 0.24),
            flex 0.7s cubic-bezier(0.45, 0.01, 0.33, 0.24),
            background 0.2s,
            -webkit-filter 0.7s cubic-bezier(0.45, 0.01, 0.33, 0.24);
    transition: font-size 0.7s cubic-bezier(0.45, 0.01, 0.33, 0.24),
            filter 0.7s cubic-bezier(0.45, 0.01, 0.33, 0.24),
            flex 0.7s cubic-bezier(0.45, 0.01, 0.33, 0.24),
            background 0.2s;
    transition: font-size 0.7s cubic-bezier(0.45, 0.01, 0.33, 0.24),
            filter 0.7s cubic-bezier(0.45, 0.01, 0.33, 0.24),
            flex 0.7s cubic-bezier(0.45, 0.01, 0.33, 0.24),
            background 0.2s,
            -webkit-filter 0.7s cubic-bezier(0.45, 0.01, 0.33, 0.24);
    
}


 .flex-class > *{
    -webkit-filter:grayscale(0%);
            filter:grayscale(0%);
    margin: 0;
    width: 100%;
    -webkit-transition: -webkit-transform 0.5s;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
    flex: 0 0 auto;
   -webkit-flex: 0 0 auto;
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    height: 90px;
    align-items: center;
    text-transform: uppercase;
    text-shadow: 0 0 4px rgba(0, 0, 0, 0.72), 0 0 14px rgba(0, 0, 0, 0.45);
    font-size: 1em;
  } 
  .flex-class >p{
      height: 90px;
      width:95%;
  } 
  .flex-class > button {
    font-size: 14px;
    width:50%;
    height: 50px;
    background:transparent;
    background-color: transparent;
    border-color:rgba(1,1,1,0.2);
    border-style: solid;
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    align-items:flex;
    margin-top:3rem;
  }
  .flex-class.flex-class-open,.flex-class.flex-class-open:active{
    flex:5 1;
    -webkit-box-flex: 5;
    -webkit-filter:grayscale(30%);
            filter:grayscale(30%);
}

  .flex-class.flex-class-open > button{
     background: var(--base-color);
    line-height: 1;
    width:70%;
    font-size:16px;
    font-weight: bold;
  }


.flex-class:nth-child(1){ background-image:url(https://source.unsplash.com/d2MSDujJl2g/1500x1500); }
.flex-class:nth-child(2) { background-image:url(https://source.unsplash.com/iRsktlI-hls/1500x1500)}
.flex-class:nth-child(3){ background-image:url(https://source.unsplash.com/YGmk9UZMdZg/1500x1500); }



.flex-class > *:first-child { -webkit-transform: translateY(-150%); transform: translateY(-150%); font-size:20px;}
.flex-class.flex-class-open > *:first-child { -webkit-transform: translateY(0); transform: translateY(0); -webkit-transition-delay: 0.4s; transition-delay: 0.4s; font-weight: bold; }
.flex-class > *:last-child { -webkit-transform: translateY(150%); transform: translateY(150%);font-size:14px; }
.flex-class.flex-class-open > *:last-child { -webkit-transform: translateY(0); transform: translateY(0);-webkit-transition-delay: 0.4s;transition-delay: 0.4s; }

.thirdDiv{
    position:relative;
    width: 100%;
    height: auto;
    display: flex;
    display: -webkit-flex;
    justify-content: flex-start;
    text-align: center;
    flex-direction: column;
    z-index: 3;
    background-color: #d8d8d8;
    margin-top: 5rem;
    padding:0 1rem;
}
.thirdDiv h2{
    background-color: #d8d8d8;
    width:300px;
    margin: 0 auto;
}
.thirdDiv::before{
    content:'';
    position: absolute;
    z-index: -1;
    left: 0;
    margin-left:1%;
	top:0.8rem;
    width: 97.5%;
    height:100%;
    border:1px solid var(--base-color);
    /* background-color: ; */
}

.discount{
    display: none;
    visibility: hidden;
}
.secondDiv,.fourthDiv{
    height: 0px;
    position:relative;
    width: 100%;
    background-color: #d8d8d8;
}
.secondDiv{
    height: 63px;
}
 .secondDiv:after,.fouthDiv:after{
    position:absolute;
    width: 100%;
    height: 60px;
    content: '';
    top:0;
    right:0;
    left:0;
    z-index:4;
    -webkit-transform-origin: top left;
            transform-origin: top left;
    -webkit-transform: skewY(-3deg);
            transform: skewY(-3deg);
    overflow: hidden;
    
}


.slanted{
    font-size: 20px;
    position: absolute;
    width: 100%;
    height: 60px;
    top: 0;
    left: 0;
    bottom:0;
    text-align: center;
    text-transform: uppercase;
    -webkit-transform-origin: top left;
            transform-origin: top left;
    -webkit-transform: rotate(-3deg) skew(-3deg);
            transform: rotate(-3deg) skew(-3deg);
    z-index:10;
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    margin: 0;
    cursor: pointer;
    -webkit-transition: all 0.5s ease-in;
    transition: all 0.5s ease-in;
    font-weight: bold;
    background-color: var(--slanted-color);
}
.fourthDiv .slanted{
    background-color: var( --base-color);
}

.slanted span{
    padding-left: 0.5rem;
    font-size: 30px;
}

.slanted::before{
	content:'';
    position: absolute;
    z-index: -10;
	left: 0px;
	/* bottom: 0px; */
	width: 0;
	height: 100%;
	background: var(--linear-menu-color);
    -webkit-transition: all 0.5s ease-in;
    transition: all 0.5s ease-in;

}
.slanted:hover:before, .slanted:hover, .slanted:active:before,.slanted:active  {
   width:100%;
   font-size: 1.40rem;
}



