
.price-wrap{
    width:100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: nowrap;
    margin-bottom: 2rem;
}

.price-first-view{
    width:100%;
    background-color: var(--base-color);
    height: 15vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
}
.price-first-view::before{
    content:'';
    position: absolute;
    z-index: -1;
	left: 0px;
	/* bottom: 0px; */
	width: 100%;
	height:19vh;
    background-color: var(--base-color);
    transform: rotate(-3deg);
    transform-origin: bottom;
}

.price-list{
    position: relative;
    z-index:1;
    width:100%;
    height:auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 1px;
    margin-top: 2rem;
}
.price-list::before{
    top:10px;
    content:'';
    position: absolute;
    z-index: -1;
    left: 0px;
    /* top:32px; */
    /* bottom:0; */
    width: 97.5%;
    margin-left: 1%;
    height:98%;
   border:1px solid var(--base-color);
}
.price-list h3{
    background-color: #d8d8d8;
    padding-left: 10px;
    padding-right: 10px;
    font-size: 1.3rem;
}

table
{
border-collapse: collapse;
border-spacing: 0px;
width:95%;
margin-bottom: 1rem;
text-align: center;
margin-top: 0.5rem;
font-size: 1.2rem;
}
table, th, td
{
padding: 5px;
border: 1px solid black;
}
table th{
    background-color: var(--slanted-color)
}
tr th:first-child{
    width:60%;
}
.Microneedling{
    margin-top: 3rem;
}
tr > td:nth-child(even){
    background-color: var(--base-color);
}

@media screen and (min-width: 670px) {
    .price-first-view::before{
        height:20vh;
    }


}
@media screen and (min-width: 800px) {
    .price-first-view::before{
        height:22.7vh;
    }
    .price-first-view{
        font-size: 2.5rem;
    }
    .Microneedling{
        margin-top: 5rem;
    }
    .price-list{
        width:80%;
    }
}

@media screen and (min-width: 1000px) {
    .price-first-view::before{
        height:25.3vh;
    }
    .Microneedling{
        margin-top: 6rem;
    }
}