.navbar {
    width:100%;
    height:10vh;
    position: relative;
    top:0;
    right:0;
    left:0;
    z-index:10001;
  }
  .header{
    width:100%;
    height: 10vh;
    background:linear-gradient(to bottom, transparent 90%, #d8d8d8 130%),var(--linear-menu-color);
    object-fit: cover;
    position: fixed;
    overflow: hidden;
    top:0;
    right: 0;
    left:0;
    display: flex;
    flex-basis: row;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: center;
    padding-left: 1rem;
    padding-right: 1rem;
    font-family: 'Proza Libre', sans-serif;
    
  }
  
  .logo{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }
  
  .menu {
    display: none;
  } 
  .menu-sideDrawer{
    width: 320px;
    background:linear-gradient(0.45turn,var(--base-color),var(--slanted-color));
    position: fixed;
    top:0;
    left:0;
    height: 100%;
    z-index:10000;
    transform:translateX(-100%);
    transition: transform 0.5s ease-in;
  }
  .menu-sideDrawer .menu {
    width:100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-end;
    align-items:center;
    transform:translateX(-100%);
    transition: transform 0.8s cubic-bezier(0.45, -0.25, 0.29, 0.38);
  }
  
  .menu-sideDrawer.open .menu{
    transform: translateX(0);
  }
  
  .menu-sideDrawer .menu .menu-wrapper{
    width:100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
    align-items:center;
    padding-left:1rem;
    padding-right:1rem;
  
  }
  .menu-sideDrawer.open{
    transform: translateX(0);
  }
  .hamburger-menu{
    width: 20px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.7rem;
    position: absolute;
    cursor: pointer;
  }
   
  .menu-sideDrawer .links{
    width:100%;
    height:50px;
    display: flex;
    justify-content: center;
    align-items: center;
    border:1px solid black;
    font-size: 2rem;
    margin-top: 1rem;
    margin-bottom:1rem;
  }
 .links a{
    width: 100%;
    height: 100%;
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    padding:0 2px;
  }
  .hamburger-menu .fa-times{
    display:none;
}
  .hamburger-menu.open .fa-times{
      display:block;
 
  }
.hamburger-menu.open .fa-bars{
    display:none;
    overflow: hidden;

} 
/*   
.hamburger-menu.open {}
  
   */
  
  
  
  @media screen and (min-width: 800px) {
    .menu-sideDrawer,.hamburger-menu{
      display: none;
      overflow: hidden;
    }
   
    .logo{
        width:200px;
    }
    .menu {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        align-items: center;
    }
    .menu .menu-wrapper{
        width:85%;
        max-width: 700px;
        height: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: flex-end;
    }
    .menu .links{
        /* width:20%; */
        /* padding-left: 4px;
        padding-right: 4px; */
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        color:white;
        border-bottom:3px solid transparent;
        
    }
     .links:hover {
       
        border-bottom:3px solid var(--base-color);
    } 
    .links:hover a{
      transform: scale(1.15) skew(-3deg);
        background-color:transparent;
    }
  }