
.Treatment-Wrap{
    width:100%;
    height: auto;
    min-height: 100vh;
    display:flex;
    margin: 0 auto;
    justify-content: flex-start;
    margin-bottom: 1rem;
}
.treatments{
    width:100%;
    height: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    margin-top:  3rem;
}
.treatment-card:nth-of-type(odd),.treatment-card:nth-of-type(even){
    width:90%;
    height: 35vh;
    border: 2px solid var(--base-color);
    background-color: var(--slanted-color);
    margin-bottom: 1rem;
    display: flex;
    justify-content: center;
    align-items:center;
    font-size: 1.5rem;
    position: relative;
}

.treatment-card:nth-of-type(even){
   background-color: var(--base-color);
   border-color: var(--slanted-color);
}
.treatment-card h3{
    opacity: 1;
    transition: 0.5s ease-in;
}

.treatment-card:hover{
   transform: scale(1.15);
}

.treatment-content{
    transition: .5s ease-in;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    width:100%;
    height: 100%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    /* background-color: pink; */
}
.treatment-content p{
    width:100%;
    height: 100%;
    transition: .5s ease-in;
    padding-left: 5px;
    padding-right: 5px;
}
.treatment-card:hover .treatment-content{
    opacity: 1;
   
}
.treatment-card:hover h3{
    opacity: 0;
}

@media screen and (min-width: 800px) {
   
    .treatments{
        margin-top: 5rem;
    }
    .treatment-card:nth-of-type(odd),.treatment-card:nth-of-type(even){
        width: 45%;
    }
}
